header.priceStyle {
  background: white;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
}
header.priceStyle img.logo {
  position: absolute;
  width: 33px;
  top: 41px;
  left: 60px;
}
header.priceStyle nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
header.priceStyle nav ul {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  margin-top: 52px;
  margin-right: 65px;
}
header.priceStyle nav ul li {
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 0 23px;
}
header.priceStyle nav ul li a {
  color: grey;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 20px;
}
header.priceStyle div.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100%;
}
header.priceStyle div.content h1 {
  color: black;
  font-size: 60px;
}
header.priceStyle div.content div.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
header.priceStyle p.additionalBlocks {
  text-align: center;
  text-decoration: none;
  color: grey;
  border: 1px solid grey;
  background: white;
  padding: 35px;
  border-radius: 50px;
  width: 610px;
  font-size: 30px;
  margin-bottom: 15px;
  letter-spacing: 5px;
  cursor: pointer;
}
header.priceStyle p.additionalBlocks-active {
  display: none;
}
header.priceStyle p.finalPrice {
  text-align: center;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  border: 1px solid black;
  background: black;
  padding: 15px;
  border-radius: 50px;
  width: 655px;
  font-size: 60px;
  margin-bottom: 115px;
  letter-spacing: 2px;
}
@media screen and (max-width: 700px) {
  header.priceStyle nav ul li.priceButton {
    display: none;
  }
}



